@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1080px !important;
  }
  .container.c-blog,
  .container-lg.c-blog,
  .container-md.c-blog,
  .container-sm.c-blog,
  .container-xl.c-blog,
  .container-xxl.c-blog {
    max-width: 900px !important;
  }
}
@media (min-width: 1400px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1200px !important;
  }
  .container.c-blog,
  .container-lg.c-blog,
  .container-md.c-blog,
  .container-sm.c-blog,
  .container-xl.c-blog,
  .container-xxl.c-blog {
    max-width: 1000px !important;
  }
}
section.page {
  margin: 120px 0 60px;
}
section.page h1 {
  margin-bottom: 40px;
}

#page-building {
  margin-bottom: 0;
}
#page-building h1 {
  margin: 16px 0;
}
#page-building h3 p {
  font-size: 16px;
}
#page-building hr {
  border-top: 2px solid #999999;
  opacity: 1;
  margin: 5px 0 21px;
}
#page-building .experiencia {
  margin-top: 10px;
}
#page-building .middle {
  margin: 80px auto;
}
#page-building .video {
  padding: 0;
}
#page-building .video video {
  max-height: 400px;
}

#page-portfolio #portfolio-categories {
  border-bottom: 2px solid #999999;
}
#page-portfolio #portfolio-categories h1 {
  margin-bottom: 0;
}
#page-portfolio #portfolio-categories .category-btn {
  font-size: 18px !important;
  margin: 10px 20px;
}
#page-portfolio #portfolio-categories .div-search-portfolio i {
  color: #ec0001;
  margin-left: 10px;
  cursor: pointer;
}
#page-portfolio #portfolio-categories .div-search-portfolio input {
  display: none;
}
#page-portfolio #portfolio-categories .div-search-portfolio input.active {
  display: block;
}

#page-blog #banner-blog .slider-blog-item {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 60vh;
}
#page-blog #banner-blog .slider-blog-item .slider-blog-text {
  position: absolute;
  left: 50%;
  bottom: 70px;
  transform: translateX(-50%);
  color: #fff;
  z-index: 3;
}
#page-blog #banner-blog .slider-blog-item .slider-blog-text h2 {
  font-weight: 300;
  margin-bottom: 20px;
}
#page-blog #banner-blog .slider-blog-item .slider-blog-text p {
  font-weight: 300;
  color: #fff;
}
#page-blog #banner-blog .slider-blog-item::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: #000;
  opacity: 0.3;
}
#page-blog #banner-blog .slick-dots {
  bottom: 30px !important;
  right: 0 !important;
  width: 100% !important;
}
#page-blog #banner-blog .slick-dots li {
  width: 100px !important;
  height: 5px !important;
}
#page-blog #banner-blog .slick-dots li button {
  width: 100px !important;
  height: 5px !important;
}
#page-blog #banner-blog .slick-dots li button::before {
  width: 100px !important;
  height: 5px !important;
  content: "" !important;
  background-color: #fff;
}
#page-blog #banner-blog .slick-dots li.slick-active button::before {
  opacity: 1 !important;
}
#page-blog #blog {
  padding: 40px 0;
}
#page-blog #blog .div-search {
  position: relative;
  margin-bottom: 50px;
}
#page-blog #blog .div-search h1 {
  margin-bottom: 20px;
}
#page-blog #blog .div-search input {
  padding: 10px 15px;
  width: 100%;
  background-color: #f3f3f3;
  border: 1px solid #ddd;
  border-radius: 30px;
}
#page-blog #blog .div-search button {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  color: #ec0001;
  border: none;
  cursor: pointer;
}
#page-blog #blog #blog-categories .div-btns {
  border-bottom: 2px solid #999999;
  margin-bottom: 50px;
  width: max-content;
}
#page-blog #blog #blog-categories .blog-category-btn {
  position: relative;
  display: inline-block;
  padding: 10px 0;
  margin-right: 30px;
  border: none;
  background: none;
  color: #000;
  cursor: pointer;
}
#page-blog #blog #blog-categories .blog-category-btn::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 0;
  height: 2px;
  background-color: #ec0001;
  transition: all ease 0.4s;
}
#page-blog #blog #blog-categories .blog-category-btn.active::before {
  width: 100%;
}
#page-blog #blog #blog-categories .blog-category-btn:nth-last-child(1) {
  margin-right: 0;
}
#page-blog #blog #blog-posts {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5%;
}
#page-blog #blog #blog-posts .post a {
  color: #000;
  text-decoration: none;
  transition: all ease 0.3s;
}
#page-blog #blog #blog-posts .post a .post-thumbnail {
  border: 2px solid #dedede;
  margin-bottom: 15px;
}
#page-blog #blog #blog-posts .post a .post-thumbnail img {
  width: 100%;
  height: auto;
}
#page-blog #blog #blog-posts .post a .post-content h2 {
  font-size: 20px !important;
}
#page-blog #blog #blog-posts .post a .post-content span {
  font-size: 14px !important;
  font-weight: 500;
}
#page-blog #blog #blog-posts .post a:hover {
  opacity: 0.6;
}

#page-search span {
  color: #ec0001 !important;
}
#page-search .cards-search {
  margin-bottom: 30px;
}
#page-search .cards-search a {
  color: #000;
  text-decoration: none;
  transition: all ease 0.3s;
}
#page-search .cards-search a h3 {
  font-size: 18px;
  margin-top: 15px;
}
#page-search .cards-search a .descricao {
  font-size: 14px;
}
#page-search .cards-search a img {
  width: 100%;
  height: 220px;
}
#page-search .cards-search a:hover {
  opacity: 0.6;
}

#page-contato h2 {
  font-size: 26px !important;
  margin-bottom: 15px;
}
#page-contato h4 {
  font-size: 16px !important;
  margin: 30px 0 10px;
}
#page-contato a {
  font-size: 16px;
  color: #000;
  text-decoration: none;
  transition: all ease 0.3s;
}
#page-contato a:hover {
  color: #ec0001;
}
#page-contato .fale-conosco p {
  font-weight: 300;
}